import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import auth from './auth'

Vue.use(Vuex, axios)

export default new Vuex.Store({
  state: {
    players: [],
    matches: [],
    arbiters: [],
    loadingStatus: false

  },
  mutations: {
    SET_PLAYERS(state, data){
      state.players = data
    },
    SET_MATCHES(state, data){
      state.matches = data
    },
    SET_ARBITERS(state, data) {
      state.arbiters = data
    },
    SET_LOADING_STATUS(state, newLoadingStatus) {
      state.loadingStatus = newLoadingStatus
    }
  },
  actions: {
    
    getPlayers({commit}){
      commit('SET_LOADING_STATUS', true)
      axios.get("/api/players")
      .then(response => response.data)
      .then(data =>{
        commit('SET_PLAYERS', data)
        commit('SET_LOADING_STATUS', false)
      })
    },
    getMatches({commit}){
      commit('SET_LOADING_STATUS', true)
      axios.get("/api/matches")
      .then(response => response.data)
      .then(data =>{
        commit('SET_MATCHES', data)
        commit('SET_LOADING_STATUS', false)
      })
    },
    getArbiters( { commit }) {
      commit('SET_LOADING_STATUS', true)

      axios.get("https://mhc-oss-api.herokuapp.com/api/teams/heren2")
      .then(response => response.data.arbiters)
      .then(data => {
        commit('SET_LOADING_STATUS', false)
        commit('SET_ARBITERS', data)
      })
      
      .catch(error => console.log(error))
    }
    
  },
  getters:{
      loadingStatus (state) {
        return state.loadingStatus
      }
  },
  modules: {
    auth
  }
})
