<template>
  <div>
    <v-subheader class="page_title">Login</v-subheader>
    <v-card class="pa-3">
      <v-alert
        :value="true"
        type="error"
        v-if="error && !success"
      >Er was een fout, niet in staat om de login te voltooien</v-alert>
      <v-form>
        <v-text-field type="text" v-model="user.email" label="E-mail"></v-text-field>
        <v-text-field type="password" v-model="user.password" label="Password"></v-text-field>
        <v-btn @click.prevent="submit" color="primary">Login</v-btn>
      </v-form>
    </v-card>
  </div>
</template>

<script>
import axios from "axios"
import { mapActions } from "vuex"
export default {
  data() {
    return {
      user:{
        email: "",
        password: "",
        
      },
      error: false
    };
  },
  methods: {
    ...mapActions({
      logUserIn: 'auth/logUserIn'
    }),
    submit (){
      this.logUserIn(this.user).then(()=> {
        this.$router.push('/')
      }).catch((error) => [
        console.log(error)
      ])
    }
  }
};
</script>

<style lang="sass">
.page_title
  padding: 0
</style>