<template>
  <div>
    <v-data-table
      :loading="loadingStatus"
      loading-text="Loading... Please wait"
      :headers="headers"
      :items="players"
      :items-per-page="-1"
      sort-by="points"
      sort-desc
      hide-default-footer
      :mobile-breakpoint="100"
      item-key="_id"
      class="elevation-1"
    >
      <template v-slot:item.name="props" v-if="isAuthenticated">
        <v-edit-dialog
          :return-value.sync="props.item.name"
          large
          persistent
          @save="save(props.item)"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <div>{{ props.item.name }}</div>
          <template v-slot:input>
            <div class="mt-4 title">Update name</div>
          </template>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.name"
              label="Edit goals"
              single-line
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.goals="props" v-if="isAuthenticated">
        <v-edit-dialog
          :return-value.sync="props.item.goals"
          large
          persistent
          @save="save(props.item)"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <div>{{ props.item.goals }}</div>
          <template v-slot:input>
            <div class="mt-4 title">Update goals</div>
          </template>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.goals"
              label="Edit goals"
              single-line
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.assists="props" v-if="isAuthenticated">
        <v-edit-dialog
          :return-value.sync="props.item.assists"
          large
          persistent
          @save="save(props.item)"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <div>{{ props.item.assists }}</div>
          <template v-slot:input>
            <div class="mt-4 title">Update assists</div>
          </template>
          <template v-slot:input>
            <v-text-field 
              v-model="props.item.assists"
              label="Edit assists"
              single-line
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
    </v-data-table>

    <v-snackbar v-model="snack" :timeout="3000" :color="snackColor">
      {{ snackText }}
      <template v-slot:action="{ attrs }">
        <v-btn v-bind="attrs" text @click="snack = false">Close</v-btn>
      </template>
    </v-snackbar>

    <v-card-text v-if="isAuthenticated" style="height: 80px;" position="relative">
      <v-btn
        class="mx-2"
        absolute
        right
        bottom
        fab
        small
        dark
        color="warning"
        @click="dialog = !dialog"
      >
        <v-icon>mdi-plus</v-icon>
      </v-btn>
    </v-card-text>

    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Speler toevoegen</span>
        </v-card-title>
        <v-card-text>
          <v-text-field v-model="playerName" label="Naam" required></v-text-field>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="dialog = false">Annuleren</v-btn>
          <v-btn color="blue darken-1" text @click="addPlayer()">Opslaan</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from "axios";
import { mapState, mapGetters } from "vuex";
export default {
  data: () => ({
    snack: false,
    snackColor: "",
    snackText: "",
    dialog: false,
    playerName: "",
    hasError: false,
    errorMessage: "",
    headers: [
      {
        text: "Spelers",
        align: "left",
        sortable: true,
        value: "name",
      },
      {
        text: "Goals",
        value: "goals",
        align: "center",
      },
      {
        text: "Assists",
        value: "assists",
        align: "center",
      },
      {
        text: "Punten",
        value: "points",
        align: "center",
      },
    ],
  }),
  computed: {
    loadingStatus() {
      return this.$store.getters.loadingStatus
    },
    ...mapState(["players"]),
    ...mapGetters({
        isAuthenticated: 'auth/isAuthenticated'
      })
  },
  created() {
    this.$store.dispatch("getPlayers");
  },
  methods: {
    addPlayer() {
      axios
        .post("/api/players", {
          name: this.playerName,
        })
        .then((response) => {
          this.dialog = false;
          this.playerName = "";
          this.$store.dispatch("getPlayers", response.data);
        });
    },
    save(player) {
      axios
        .put(`/api/players/${player.id}`, {
          name: player.name,
          goals: player.goals,
          assists: player.assists,
        })
        .then((response) => {
          if (response.status === 200) {
            this.snack = true;
            this.snackColor = "success";
            this.snackText = "Data saved";
            this.$store.dispatch("getPlayers", response.data);
          }
        });
    },
    cancel() {
      this.snack = true;
      this.snackColor = "error";
      this.snackText = "Canceled";
    },
    open() {
      this.snack = true;
      this.snackColor = "info";
      this.snackText = "Dialog opened";
    },
    close() {
      console.log("Dialog closed");
    },
  },
};
</script>

