<template>
  <v-flex>
    <v-progress-linear
      v-if="loadingStatus"
      indeterminate
      color="primary"
    ></v-progress-linear>
    <v-alert
      v-if="arbiters.length <= 0 && !loadingStatus"
      class="black--text"
      :value="true"
      type="warning"
      >Er zijn op dit moment geen wedstrijden om te fluiten.
    </v-alert>
    <v-card>
      <v-card tile flat v-for="arbiter in arbiters" :key="arbiter.id">
        <v-card-title primary-title class="py-4">
          <div>
            <h4 v-if="arbiter.matchDate">{{ arbiter.matchDate }}</h4>
            <p class="body-1 d-inline-block mr-4 mb-0">{{ arbiter.teams }}</p>
            <p class="caption d-inline-block match_time mb-0">
              {{ arbiter.matchTime }}
            </p>
            <p class="body-2 mb-0">{{ arbiter.umpires }}</p>
            <p class="body-2 mb-0">{{ arbiter.field }}</p>
            <p class="body-2 mb-0">{{ arbiter.location }}</p>
          </div>
        </v-card-title>
      </v-card>
    </v-card>
  </v-flex>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    loadingStatus() {
      return this.$store.getters.loadingStatus;
    },
    ...mapState(["arbiters"]),
  },
  created() {
    this.$store.dispatch("getArbiters");
  },
};
</script>

<style lang="sass">
.match_time
  background: #f1f1f1
  padding: 4px
  border-radius: 4px
</style>