<template>
  <div>
    <v-card>
      <v-progress-linear v-if="loadingStatus" indeterminate color="primary"></v-progress-linear>
      <v-card-text class="py-0">
        <v-timeline align-top dense v-for="(match) in matches" :key="match.index">
          <v-timeline-item small color="warning">
            <v-row class="pt-1">
              <v-col cols="5">
                <strong class="d-block">{{match.matchDate}}</strong>
                <strong class="font-weight-light" >{{match.matchTime}}</strong>
              </v-col>
              <v-col cols="6">
                <strong>{{match.homeTeam}}</strong>
                <div
                  v-for="driver in match.driverIDs"
                  :key="driver._id"
                  class="caption"
                >{{driver.name}}</div>
              </v-col>
              <v-col cols="1" class="away-uniform ml-n2"  v-if="match.awayUniform === true">
                <v-icon color="#ffd907">mdi-tshirt-crew</v-icon>
              </v-col>
            </v-row>
            <v-select v-if="isAuthenticated"
            class="d-block"
            clearable
            :items="players"
            item-text="name"
            item-value="_id"
            :menu-props="{ maxHeight: '300' }"
            label="Select"
            multiple
            single-line
            hint="Rijders toevoegen"
            v-model="match.selectedPlayers"
            ></v-select>
            <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn v-if="isAuthenticated"
              small
              color="primary"
              @click="addDriver(match._id, match.selectedPlayers);"
            >Opslaan</v-btn>
            </v-card-actions>
          </v-timeline-item>
        </v-timeline>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import axios from "axios";
import { mapGetters, mapState } from "vuex";

export default {
  data: () => ({
    selectedPlayers: []
  }),
  computed: {
    loadingStatus() {
      return this.$store.getters.loadingStatus;
    },
    ...mapState(["players", "matches"]),
    ...mapGetters({
        isAuthenticated: 'auth/isAuthenticated'
      })
  },
  created() {
    this.$store.dispatch("getPlayers");
    this.$store.dispatch("getMatches");   
  },
  methods: {
    addDriver(matchId, drivers) {
      axios
        .put(`/api/matches/${matchId}`, {
          driverIDs: drivers,
        })
        .then((response) => {
          this.$store.dispatch("getMatches", response.data);
        });
    },
  },
};
</script>

<style scoped>
.col {
  padding: 0;
}
.away-uniform {
  text-align: center;
}
</style>
